<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <div style="font-size: 18px;font-weight: bold;text-align: center;margin: 10px 0 10px 0;color:grey">设备信息</div>
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/deviceList" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">设备列表</span>
        </el-menu-item>
        <el-menu-item index="/childDevice" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">部件表</span>
        </el-menu-item>
        <el-menu-item index="/meterList" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">仪表列表</span>
        </el-menu-item>
        <div style="font-size: 18px;font-weight: bold;text-align: center;margin: 10px 0 10px 0;color:grey">点检标准录入</div>
        <el-menu-item index="/entryForm" >
          <i class="el-icon-caret-right"></i>
          <span slot="title">标准录入</span>
        </el-menu-item>
        <el-menu-item index="/standardView" >
          <el-dropdown placement="bottom">
            <div>
              <i class="el-icon-location-outline"></i>
              <span slot="title">设备标准</span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$router.push('/standardView')">运行工标准</el-dropdown-item>
              <el-dropdown-item @click.native="$router.push('/standardView2')">维修工标准</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
        <el-menu-item index="/meterList1" >
          <el-dropdown placement="bottom">
            <div>
              <i class="el-icon-location-outline"></i>
              <span slot="title">仪表标准</span>
            </div>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="$router.push('/meterList1')">运行工标准</el-dropdown-item>
              <el-dropdown-item @click.native="$router.push('/meterList2')">维修工标准</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 40px">
        <img src="../../assets/金色时代.png" height="234" width="153"/>
      </div>
    </div>
    <div style="padding: 20px 0 0 20px">
      <div style="padding-bottom:20px">
        <el-input style="width: 300px;margin-right: 10px" placeholder="请输入搜索内容" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)">查询</el-button>
        <el-button type="info" @click="reset">重置</el-button>
        <el-button type="danger" plain @click="delBatch" v-if="user.authority==='超级管理员'">批量删除</el-button>
      </div>

      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="ID" width="60" prop="id" align="center" :show-overflow-tooltip="true"></el-table-column>
<!--        <el-table-column label="NFC标签值" width="120" prop="nfcNum"  align="center"></el-table-column>-->
<!--        <el-table-column label="录入形式" width="120" prop="entry"  align="center"></el-table-column>-->
        <el-table-column label="设备名称" prop="equipmentName"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="子设备名称" width="120" prop="childEquipment"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="参数名称" width="120" prop="parameter"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="参数类型" width="80" prop="type"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="开机标准" prop="powerUp" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="关机标准" width="180" prop="powerDown" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="标准描述" width="240" prop="standardDes" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="操作" align="center" width="180" fixed="right">
          <template v-slot="scope">
            <el-button size="mini" type="primary" plain @click="handleEdit(scope.row)" v-if="user.authority==='管理员' || user.authority==='超级管理员'">编辑</el-button>
            <el-button size="mini" type="danger" plain  @click="del(scope.row.id)" v-if="user.authority==='超级管理员'">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="新增设备参数数据范围" :visible.sync="fromVisible" width="40%">
        <el-form :model="form" label-width="100px" style="padding-right: 20px" :rules="rules" ref="formRef">
<!--          <el-form-item label="NFC标签值" prop="nfc_num"><el-input v-model="form.nfc_num" placeholder="NFC标签值"></el-input></el-form-item>-->
<!--          <el-form-item label="录入形式" prop="entry"><el-input v-model="form.entry" placeholder="录入形式"></el-input></el-form-item>-->
          <el-form-item label="设备名称" prop="equipmentName"><el-input v-model="form.equipmentName" placeholder="设备名称"></el-input></el-form-item>
          <el-form-item label="子设备名称" prop="childEquipment"><el-input v-model="form.childEquipment" placeholder="子设备名称"></el-input></el-form-item>
          <el-form-item label="参数名称" prop="parameter"><el-input v-model="form.parameter" placeholder="参数名称"></el-input></el-form-item>
          <el-form-item label="参数类型" prop="type"><el-input v-model="form.type" placeholder="参数类型"></el-input></el-form-item>
          <el-form-item label="开机范围" prop="powerUp"><el-input v-model="form.powerUp" placeholder="开机范围"></el-input></el-form-item>
          <el-form-item label="关机范围" prop="powerDown"><el-input v-model="form.powerDown" placeholder="关机范围"></el-input></el-form-item>
          <el-form-item label="标准描述" prop="standardDes"><el-input v-model="form.standardDes" placeholder="标准描述"></el-input></el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false">取 消</el-button>
          <el-button type="primary" @click="save">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>


</template>

<script>
export default {
  name: "StandardView2",
  data() {
    return {
      isCollapse: false,  // 不收缩
      searchText: '',
      tableData: [],  // 所有的数据
      pageNum: 1,   // 当前的页码
      pageSize: 10,  // 每页显示的个数
      department: '',
      equipmentName: '',
      total: 0,
      fromVisible: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      ids: []
    }
  },
  created() {
    this.load()
  },
  methods: {
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('请选择数据')
        return
      }
      this.$confirm('您确认批量删除这些数据吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/standardView2/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 当前选中的所有的行数据
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('您确认删除吗？', '确认删除', {type: "warning"}).then(response => {
        this.$request.delete('/standardView2/delete/' + id).then(res => {
          if (res.code === '200') {   // 表示操作成功
            this.$message.success('操作成功')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 弹出错误的信息
          }
        })
      }).catch(() => {})
    },
    handleEdit(row) {   // 编辑数据
      this.form = JSON.parse(JSON.stringify(row))  // 给form对象赋值  注意要深拷贝数据
      this.fromVisible = true   // 打开弹窗
    },
    handleAdd() {   // 新增数据
      this.form = {}  // 新增数据的时候清空数据
      this.fromVisible = true   // 打开弹窗
    },
    save() {   // 保存按钮触发的逻辑  它会触发新增或者更新
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/standardView2/update': '/standardView2/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form
          }).then(res => {
            if (res.code === '200') {  // 表示成功保存
              this.$message.success('保存成功')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 弹出错误的信息
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 分页查询
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/standardView2/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentName: this.equipmentName,
          searchText: this.searchText // 直接传递 searchText 参数
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>
